import React, {Component} from 'react';
import {Button, Col, Row, Form, Input, Select, Space, message, Divider, DatePicker, Card, Modal} from "antd";
import { updatePassword} from "../../../http/service";
import Util from "../../../assets/js/Util";
import './index.less'
import store from "../../../store";

class Index extends Component {

    formRef = React.createRef();

    constructor(props) {
        super(props);

        this.state = {
            visible:true,
            formItem: {}
        }

    }

    componentDidMount() {
    }


    operate = (form) => {

        const {password,newPassword,confirmPassword}=form;

        if(password===newPassword){
            Util.warning("")
            return;
        }

        if(confirmPassword!==newPassword){
            Util.warning("")
            return;
        }

        this.setState({
            formItem: Object.assign(this.state.formItem, form)
        })

        updatePassword(this.state.formItem).then((data) => {
            Util.success("")

            store.clear()
            this.props.history.push('/user/login')

        }).catch(err => {
            Util.error(err)
        })


    }

    render() {


        return (
            <div>
                <Row>
                    <Col span={22}>
                        <h3></h3>
                    </Col>
                </Row>
                <Divider/>
                <Row>
                    <Col span={8} offset={8}>
                        <Form
                            ref={this.formRef}
                            layout="horizontal"
                            labelCol={{span: 4}}
                            wrapperCol={{span: 20}}
                            onFinish={this.operate}
                        >
                            <Form.Item name={'password'} label={''} rules={[
                                {
                                    required: true,
                                    message: '!',
                                },
                            ]}>
                                <Input type={"password"} placeholder='' minLength={6} maxLength={50}></Input>
                            </Form.Item>

                            <Form.Item name={'newPassword'} label={''} rules={[
                                {
                                    required: true,
                                    message: '!',
                                },
                            ]}>
                                <Input type={"password"} placeholder='' minLength={6} maxLength={50}></Input>
                            </Form.Item>

                            <Form.Item name={'confirmPassword'} label={''} rules={[
                                {
                                    required: true,
                                    message: '!',
                                },
                            ]}>
                                <Input type={"password"} placeholder='' minLength={6} maxLength={50}></Input>
                            </Form.Item>

                            <Form.Item wrapperCol={{offset: 8, span: 16}}>


                                <Button style={{width: '6em'}} type="primary" htmlType="submit">
                                    
                                </Button>
                            </Form.Item>
                        </Form>
                    </Col>
                </Row>
            </div>
        );
    }
}

export default Index;