import React, {Component} from 'react';

import {Row, Col, Button, Table, Space, message, Modal, Divider, Pagination} from "antd";

import Util from "../../../assets/js/Util";

import Role from "../../../assets/js/Role";

import {getCasemanagerList,deleteCasemanager} from "../../../http/service";

class Index extends Component {

    constructor(props) {
        super(props);
        this.station_id=this.props.location.state.station_id
        this.station_name=this.props.location.state.station_name
        this.state={
            data:{
                data:[]
            }
        }
    }

    componentDidMount() {
        this.getList(1)
    }

    back=()=>{
        this.props.history.goBack()
    }

    getList=(page)=> {

        page=page||1;

        let pageSize=Util.pageSize
        pageSize=2;

        getCasemanagerList( {
            role:Role.caseManager,
            page,
            pageSize,
            station_id:this.station_id
        }).then((data) => {

            this.setState({
                data:data
            })

        }).catch(err => {
            message.warning(err)
        })
    }
    add=()=> {
        this.props.history.push({
            pathname:'/caseManager/operate',
            state:{
                station_id:this.station_id
            }
        })
    }
    edit=(row)=>{

        this.props.history.push({
            pathname:'/caseManager/operate',
            state:{
                opType:'edit',
                formItem:row,
                station_id:this.station_id
            }
        })
    }
    delete=(casemanager_id)=>{

        Util.confirm({
            title:'',
            onOk:()=>{
                deleteCasemanager({
                    casemanager_id,
                    station_id:this.station_id
                }).then(()=>{
                    message.success("")
                    this.getList()
                }).catch(err=>{
                    message.warning(err)
                })
            }
        })

    }



    render() {

        const columns = [
            {
                title: '',
                dataIndex: 'index',
                render:(text,row,index)=>{
                    return `${(this.state.data.currentPage-1)*(this.state.data.pageSize)+(index+1)}`
                }
            },
            {
                title: '',
                dataIndex: 'name',
            },
            {
                title: '',
                dataIndex: 'phone',
            },
            {
                title: '',
                dataIndex: 'gender',
                render:(text)=>{
                    return text==='male'?'':''
                }
            },
            {
                title: '',
                dataIndex: 'email',
            },
            {
                title: '',
                dataIndex: 'birthday',
            },
            {
                title: '',
                dataIndex: 'action',
                render: (text, row) => (
                    <Space size="middle">
                        <Button type={"primary"} onClick={this.edit.bind(this,row)}></Button>
                        <Button type={"primary"} danger onClick={this.delete.bind(this,row.user_id)}></Button>
                    </Space>
                ),
            },
        ];

        return (
            <div>
                <Row>
                    <Col span={20}>
                        <h3>{this.station_name} / </h3>
                    </Col>
                    <Col span={2}>
                        <Button type={"primary"} onClick={this.add}></Button>
                    </Col>
                    <Col span={2}>
                        <Button type={"default"} onClick={this.back}></Button>
                    </Col>
                </Row>
                <Divider/>
                <Row>
                    <Col span={24}>
                        <Table dataSource={this.state.data.data} columns={columns} rowKey="user_id" pagination={false}/>
                        {
                            this.state.data.count > 0
                                ?
                                (<Pagination showQuickJumper total={this.state.data.count} pageSize={this.state.data.pageSize} current={this.state.data.currentPage} onChange={this.getList}/>)
                                :
                                (null)
                        }
                    </Col>
                </Row>
            </div>
        );
    }
}

export default Index;