import React, {Component} from 'react';
import {Button, Col, DatePicker, Divider, Form, Input, Pagination, Row, Modal, Space, Table} from "antd";
import Util from "../../../assets/js/Util";
import {getTherapistComplaintsByTId} from "../../../http/service";
import COMPLAINT_STATE from "../../../assets/js/constants/COMPLAINT_STATE";
import store from "../../../store";

class Index extends Component {
    formRef = React.createRef();

    constructor(props) {
        super(props);

        this.therapist_id=store.getState().user_id;

        this.state = {
            data: {
                data:[]
            },
            visible: false,
            reportContent: '',
            form: {},

        }
    }

    componentDidMount() {
        this.getList()
    }


    getList = () => {
        getTherapistComplaintsByTId(
            {
                therapist_id:this.therapist_id
            }
        ).then(data => {
            this.setState({
                data: data,
            })
        }).catch(err => {
            Util.error(err)
        })


    }


    render() {

        const columns = [
                {
                    title: '',
                    dataIndex: 'index',
                    render:(text,row,index)=>{
                        return `${(this.state.data.currentPage-1)*(this.state.data.pageSize)+(index+1)}`
                    }
                },
                {
                    title: '',
                    dataIndex: 'name',
                },
                {
                    title: '',
                    dataIndex: 'phone',
                },
                {
                    title: '',
                    dataIndex: 'complaint_date',
                },
                {
                    title: '',
                    dataIndex: 'content',
                },
                {
                    title: '',
                    dataIndex: 'state',
                    render: (text) => {
                        return text === COMPLAINT_STATE.UNHANDLED ? '' : text === COMPLAINT_STATE.REJECTED ? '' : ''
                    }
                },
            ]
        ;


        return (

            <div>

                <Row>
                    <Col span={22}>
                        <h3></h3>
                    </Col>
                </Row>
                <Divider/>

                <Row justify={'center'}>
                    <Col span={24}>
                        <Table dataSource={this.state.data.data} columns={columns} rowKey="complaint_id" pagination={false}/>
                        {
                            this.state.data.data.length>0?
                                <Pagination showQuickJumper total={this.state.data.count} onChange={this.getList} />
                                :null
                        }
                    </Col>
                </Row>
            </div>
        )
            ;
    }
}

export default Index;