import React, {Component} from 'react';
import {
    Button,
    Col,
    Row,
    Form,
    Input,
    Select,
    Space,
    message,
    Divider,
    DatePicker,
    Table,
    Pagination,
    Modal
} from "antd";
import {addDivisionAdmin, updateDivisionAdmin} from "../../../http/service";
import Util from "../../../assets/js/Util";
import Role from "../../../assets/js/Role";
import {NOTICE_SHOW_TYPE} from "../../../assets/js/constants/constant";
import {addEthicsnotice,updateEthicsnotice,getUserList} from "../../../http/service";
import moment from 'moment'

const {Option} = Select;

class Index extends Component {

    formRef = React.createRef();

    constructor(props) {
        super(props);

        this.isEdit = this.props.location.state && this.props.location.state.opType === 'edit'

        let formItem = {
            show_manner: 1,
            therapist_id:''
        }
        if (this.isEdit) {
            formItem = this.props.location.state.formItem
            formItem.therapist_name=formItem.name;
            if(formItem.end_date){
                formItem.end_date = moment(formItem.end_date)
            }

        }


        this.state = {
            formItem,
            therapistListData: {
              data:[]
            },
            visible:false,
        }

    }

    componentDidMount() {
        this.getTherapistList()
    }



    getTherapistList=(page)=>{
        page = page || 1;

        let pageSize = Util.pageSize

        getUserList({
            role:Role.therapist,
            page,
            pageSize
        }).then(data=>{
            this.setState({
                therapistListData:data
            })
        }).catch(err => {
            Util.error(err)
        })
    }


    back = () => {
        this.props.history.goBack()
    }

    close = () => {
        this.setState({
            visible:false
        })
    }
    operate = (form) => {
        form.end_date = Util.getDateFromMoment(form.end_date);


        this.setState({
            formItem: Object.assign(this.state.formItem, form)
        })


        if (this.isEdit) {

            updateEthicsnotice(this.state.formItem).then(data=>{
                Util.success("")
                this.back()
            }).catch(err => {
                Util.error(err)
            })

        } else {
            addEthicsnotice(this.state.formItem).then(data=>{
                Util.success("")
                this.back()
            }).catch(err => {
                Util.error(err)
            })
        }


    }

    relate=(row)=>{


        this.formRef.current.setFieldsValue({

            therapist_name:row.name,
        })

        this.setState({
            formItem:{
                therapist_id:row.user_id,
            },
        })

        this.close()
    }

    render() {

        const allColumns = [
            {
                title: '',
                dataIndex: 'index',
                render:(text,row,index)=>{
                    return `${(this.state.data.currentPage-1)*(this.state.data.pageSize)+(index+1)}`
                }
            },
            {
                title: '',
                dataIndex: 'name',
            },
            {
                title: '',
                dataIndex: 'phone',
            },
            {
                title: '',
                dataIndex: 'gender',
                render: (text) => {
                    return text === 'male' ? '' : ''
                }
            },
            {
                title: '',
                dataIndex: 'email',
            },
            {
                title: '',
                dataIndex: 'birthday',
            },
            {
                title: '',
                dataIndex: 'action',
                render: (text, row) => (
                    <Space size="middle">
                        <Button size={"small"} type={"primary"}
                                onClick={this.relate.bind(this, row)}></Button>
                    </Space>
                ),
            },
        ];


        return (
            <div>
                <Modal
                    width='50vw'
                    visible={this.state.visible}
                    title=""
                    onCancel={()=>{
                        this.close()
                    }}
                    footer={[
                        <Button key={1} onClick={() => {
                            this.close()
                        }}></Button>
                    ]}
                >
                    <Table dataSource={this.state.therapistListData.data} columns={allColumns} rowKey='user_id'
                           pagination={false}/>
                    {
                        this.state.therapistListData.count > 0
                            ?
                            (<Pagination showQuickJumper total={this.state.therapistListData.count}
                                         pageSize={this.state.therapistListData.pageSize}
                                         current={this.state.therapistListData.currentPage}
                                         onChange={this.getTherapistList}/>)
                            :
                            (null)
                    }
                </Modal>

                <Row>
                    <Col span={22}>
                        <h3></h3>
                    </Col>
                </Row>
                <Divider/>
                <Row>
                    <Col offset={8} span={8}>

                        <Form
                            ref={this.formRef}
                            layout="horizontal"
                            labelCol={{span: 4}}
                            wrapperCol={{span: 20}}
                            initialValues={this.state.formItem}
                            onFinish={this.operate}
                        >
                            {
                                !this.isEdit?
                                    (
                                        <Form.Item label="">
                                            <Button type={"dashed"} onClick={()=>{this.setState({visible:true})}}></Button>
                                        </Form.Item>
                                    )
                                    :
                                    (
                                       null
                                    )

                            }

                            <Form.Item name="therapist_name" label=""
                                       rules={[{required: true, message: '!'}]}>
                                <Input placeholder={''} readOnly />
                            </Form.Item>

                            <Form.Item
                                label=""
                                name="content"
                                rules={[
                                    {
                                        required: true,
                                        message: '!',
                                    },
                                ]}
                            >
                                <Input.TextArea rows={5} placeholder={''} maxLength={500}/>
                            </Form.Item>

                            <Form.Item name="show_manner" label=""
                                       rules={[{required: true, message: '!'}]}>
                                <Select
                                    placeholder=""
                                >
                                    <Option value={0}></Option>
                                    <Option value={1}></Option>
                                    <Option value={2}></Option>

                                </Select>
                            </Form.Item>
                            <Form.Item
                                noStyle
                                shouldUpdate={(prevValues, currentValues) => prevValues.show_manner !== currentValues.show_manner}
                            >

                                {({getFieldValue}) => {
                                    return getFieldValue('show_manner') === 2 ? (
                                        <Form.Item
                                            label=""
                                            name="end_date"
                                            format="YYYY-MM-DD"
                                            rules={[
                                                {
                                                    required: true,
                                                    message: '!',
                                                },
                                            ]}
                                        >
                                            <DatePicker/>
                                        </Form.Item>
                                    ) : null
                                }}

                            </Form.Item>


                            <Form.Item wrapperCol={{offset: 8, span: 16}}>


                                <Space>
                                    <Button type="default" onClick={this.back}>
                                        
                                    </Button>
                                    <Button type="primary" htmlType="submit">
                                        
                                    </Button>
                                </Space>
                            </Form.Item>
                        </Form>
                    </Col>
                </Row>
            </div>
        );
    }
}

export default Index;