import React, {Component} from 'react';

import {Row,Col,Button,Table,Space,message,Modal,Divider} from "antd";

import Util from "../../../assets/js/Util";

import Role from "../../../assets/js/Role";

import {getMeasureList,deleteMeasure} from "../../../http/service";

import store from "../../../store";

class Index extends Component {

    constructor() {
        super();
        this.state={
            dataList:[],
            isAdd:false
        }
    }

    componentDidMount() {
        this.getList()
    }


    getList=()=> {

        getMeasureList( {
        }).then((data) => {

            if(data.data.length===0){
                this.setState({
                    isAdd:true
                })
            }

            this.setState({
                dataList:data.data
            })

            if(data.roleData&&data.roleData.length>0){

                let list=this.state.dataList;
                list.unshift(data.roleData[0])
                this.setState({
                    dataList:list
                })
            }

        }).catch(err => {
            message.warning(err)
        })
    }
    add=()=> {
        this.props.history.push('/measure/operate')
    }
    edit=(row)=>{

        this.props.history.push({
            pathname:'/measure/operate',
            state:{
                opType:'edit',
                from:'/measure/list',
                formItem:row
            }
        })
    }
    detail=(row)=>{

        this.props.history.push({
            pathname:'/measure/detail',
            state:{
                measureId:row.id,
            }
        })
    }
    delete=(id)=>{

        Util.confirm({
            title:'',
            onOk:()=>{
                deleteMeasure({
                    id
                }).then(()=>{
                    message.success("")
                    this.getList()
                }).catch(err=>{
                    message.warning(err)
                })
            }
        })

    }



    render() {

        const columns = [
            {
                title: '',
                dataIndex: 'index',
                render:(text,row,index)=>{
                    return index+1;
                }
            },
            {
                title: '',
                dataIndex: 'name',
            },
            {
                title: '',
                dataIndex: 'alias',
            },
            {
                title: '',
                dataIndex: 'description',
            },
            {
                title: '',
                dataIndex: 'createtime',
            },
            {
                title: '',
                dataIndex: 'role',
                render:(role)=>{
                    return role===0?'':'';
                }
            },
            {
                title: '',
                dataIndex: 'status',
                render:(status)=>{
                    return status===1?'':'';
                }
            },
            {
                title: '',
                dataIndex: 'action',
                render: (text, row) => (
                    <Space size="middle">
                        {
                            store.getState().role===row.role?
                                <React.Fragment>
                                    <Button type={"primary"} size={"small"} onClick={this.edit.bind(this,row)}></Button>
                                    <Button type={"primary"} size={"small"} danger onClick={this.delete.bind(this,row.id)}></Button>
                                    <Button type={"primary"} size={"small"} onClick={this.detail.bind(this,row)}></Button>
                                </React.Fragment>
                                :
                                <React.Fragment>
                                    <Button type={"primary"} size={"small"} onClick={this.detail.bind(this,row)}></Button>
                                </React.Fragment>
                        }
                    </Space>
                ),
            },
        ];

        return (
            <div>
                <Row>
                    <Col span={22}>
                        <h3></h3>
                    </Col>
                    {
                        this.state.isAdd?
                            <Col span={2}>
                                <Button type={"primary"} onClick={this.add}></Button>
                            </Col>
                            :
                            null
                    }
                </Row>
                <Divider/>
                <Row>
                    <Col span={24}>
                        <Table dataSource={this.state.dataList} columns={columns} rowKey="id" pagination={false}/>
                    </Col>
                </Row>
            </div>
        );
    }
}

export default Index;