import React, {Component} from 'react';
import {Button, Col, DatePicker, Divider, Form, Input, Pagination, Row, Modal, Space, Table} from "antd";
import Util from "../../../assets/js/Util";
import {getTherapistComplaints,saveResearchReport,rejectComplaint,addBlacklist} from "../../../http/service";
import COMPLAINT_STATE from "../../../assets/js/constants/COMPLAINT_STATE";
import COMPLAINT_STATE_DESC from "../../../assets/js/constants/COMPLAINT_STATE_DESC";

const {RangePicker} = DatePicker;

class Index extends Component {
    formRef = React.createRef();
    modalRef = React.createRef();

    constructor(props) {
        super(props);

        this.state = {
            data: [],
            visible: false,
            reportContent: '',
            form: {},

        }
    }

    componentDidMount() {
        this.getList(1)
    }


    getList = (page) => {
        page = page || 1;

        let pageSize = Util.pageSize
        getTherapistComplaints(Object.assign({
            page,
            pageSize,

        }, this.state.form)).then(data => {
            this.setState({
                data: data,
            })
        }).catch(err => {
            Util.error(err)
        })


    }

    query = (form) => {

        let obj = Object.assign({}, this.state.form, form)
        if (obj.date) {
            obj.startDate = Util.getDateFromMoment(obj.date[0])
            obj.endDate = Util.getDateFromMoment(obj.date[1]) + ` 23:59:59`

        } else {
            obj.startDate = ''
            obj.endDate = ''
        }

        this.setState({
            form: obj
        })

        this.getList()
    }


    checkReport = () => {

        this.modalRef.current.submit();


    }

    saveReport=(form)=>{
        saveResearchReport({
            complaint_id:this.modalRef.current.getFieldValue('complaint_id'),
            report_content:form.report_content
        }).then(data => {
            Util.success(``)
            this.close()
            this.getList(this.state.data.currentPage)
        }).catch(err => {
            Util.error(err)
        })
    }


    /**
     * 
     * @param params
     */
    reject = (row) => {
        Util.confirm({
            title: '',
            content: '',
            onOk: () => {

                rejectComplaint({
                    complaint_id:row.complaint_id
                }).then(data => {
                    Util.success(``)
                    this.getList(this.state.data.currentPage)
                }).catch(err => {
                    Util.error(err)
                })

            },
            onCancel: () => {
            }
        });
    }
    /**
     * 
     * @param params
     */
    addBlackList = (row) => {
        Util.confirm({
            title: '',
            content: '',
            onOk: () => {

                addBlacklist({
                    complaint_id:row.complaint_id,
                    user_id:row.user_id,
                }).then(data => {
                    Util.success(``)
                    this.getList(this.state.data.currentPage)
                }).catch(err => {
                    Util.error(err)
                })

            },
            onCancel: () => {
            }
        });
    }

    close=()=>{
        this.setState({
            visible:false,
        })
    }

    show=(row)=>{
        this.setState({
            visible:true,
        })
        setTimeout(()=>{
            this.modalRef.current.setFieldsValue(row)
        })


    }


    render() {

        const columns = [
                {
                    title: '',
                    dataIndex: 'index',
                    render:(text,row,index)=>{
                        return `${(this.state.data.currentPage-1)*(this.state.data.pageSize)+(index+1)}`
                    }
                },
                {
                    title: '',
                    dataIndex: 'therapist_name',
                },
                {
                    title: '',
                    dataIndex: 'therapist_phone',
                },
                {
                    title: '',
                    dataIndex: 'name',
                },
                {
                    title: '',
                    dataIndex: 'phone',
                },
                {
                    title: '',
                    dataIndex: 'complaint_date',
                },
                {
                    title: '',
                    dataIndex: 'content',
                },
                {
                    title: '',
                    dataIndex: 'state',
                    render: (text) => {
                        return COMPLAINT_STATE_DESC[text]
                    }
                },
                {
                    title: '',
                    dataIndex: 'action',
                    render: (text, row) => {
                        return (row.state === COMPLAINT_STATE.UNHANDLED || row.state === COMPLAINT_STATE.REMOVED) ?
                            <Space size="middle">
                                <Button size={"small"} type={"primary"}
                                        onClick={this.addBlackList.bind(this, row)}></Button>
                                <Button size={"small"} type={"primary"} onClick={this.reject.bind(this, row)}
                                        danger></Button>

                                <Button size={"small"} type={"primary"}
                                        onClick={this.show.bind(this,row)}></Button>
                            </Space>
                            :
                            <Space size="middle">
                                <Button size={"small"} type={"primary"}
                                        onClick={this.show.bind(this,row)}></Button>
                            </Space>

                    }
                }
            ]
        ;


        return (

            <div>

                <Modal
                    title=""
                    visible={this.state.visible}
                    onOk={this.checkReport}
                    onCancel={this.close}
                >
                    <Form ref={this.modalRef}
                          onFinish={this.saveReport}

                    >
                        <Form.Item name={'report_content'} rules={[
                            {
                                required: true,
                                message: '!',
                            },
                        ]}>
                            <Input.TextArea rows={5} placeholder={''}></Input.TextArea>
                        </Form.Item>
                    </Form>
                </Modal>


                <Row>
                    <Col span={22}>
                        <h3></h3>
                    </Col>
                </Row>
                <Divider/>
                <Row>
                    <Col>
                        <Form
                            ref={this.formRef}
                            layout="inline"
                            labelCol={{span: 10}}
                            wrapperCol={{span: 14}}
                            onFinish={this.query}
                        >
                            <Form.Item
                                label=""
                                name="therapistName"
                            >
                                <Input key={1} placeholder={''} maxLength={11}/>
                            </Form.Item>

                            <Form.Item
                                label=""
                                name="userName"
                            >
                                <Input placeholder={''} maxLength={21}/>
                            </Form.Item>


                            <Form.Item
                                label=""
                                name="date"
                                format="YYYY-MM-DD"
                            >
                                <RangePicker style={{width:'20em'}}/>
                            </Form.Item>


                            <Form.Item wrapperCol={{offset: 8, span: 16}}>


                                <Button style={{width: '6em'}} type="primary" htmlType="submit">
                                    
                                </Button>
                            </Form.Item>
                        </Form>
                    </Col>
                </Row>
                <Divider/>

                <Row justify={'center'}>
                    <Col span={24}>
                        <Table dataSource={this.state.data.data} columns={columns} rowKey="user_id" pagination={false}/>
                        {
                            this.state.data.count > 0
                                ?
                                (<Pagination showQuickJumper total={this.state.data.count} onChange={this.getList}/>)
                                :
                                (null)
                        }
                    </Col>
                </Row>
            </div>
        )
            ;
    }
}

export default Index;