import React, {Component} from 'react';
import {Form, Input} from "antd";

class Zhidaoyu extends Component {


    constructor(props){

        super(props)

    }
    componentDidMount() {
        if(this.props.data){
            setTimeout(()=>{
                this.props.form.current.setFieldsValue({
                    name:this.props.data.name
                })
            },1)
        }
    }

    render() {
        return (
            <Form.Item name={'name'} label="" rules={[
                {
                    required: true,
                    message: '!',
                },
            ]}>
                <Input.TextArea rows={5} placeholder={''}></Input.TextArea>
            </Form.Item>
        );
    }
}

export default Zhidaoyu;
