import React, {Component} from 'react';

import {Row, Col, Button, Table, Space, message, Modal, Divider, Pagination, Form, Input} from "antd";

import Util from "../../../assets/js/Util";

import {getRelateTherapistList, deleteRelateTherapist} from "../../../http/service";

import store from "../../../store";

import Role from "../../../assets/js/Role";

import TherapistList from './components/TherapistList'

class Index extends Component {

    constructor(props) {
        super(props);


        this.getStationInfo();



        this.state = {
            data: {
                data: []
            },
            visible: false
        }
    }

    componentDidMount() {
        this.getList(1)
    }

    getStationInfo=()=>{
        //idid

        if(store.getState().role===Role.divisionManager){
            this.station_id = this.props.location.state.station_id
            this.station_name = this.props.location.state.station_name
        }else{
            this.station_id = store.getState().station_id
            this.station_name = store.getState().station_name
        }
    }

    getList = (page) => {

        page = page || 1;

        let pageSize = Util.pageSize

        getRelateTherapistList({
            page,
            pageSize,
            station_id: this.station_id

        }).then((data) => {

            if (data) {
                this.setState({
                    data: data
                })
            }

        }).catch(err => {
            message.warning(err)
        })
    }
    open = () => {
        this.setState({
            visible: true
        })
    }
    back = () => {

        this.props.history.goBack()
    }

    close = () => {

        this.setState({
            visible: false
        })

        this.getList()
    }

    remove = (therapist_id) => {

        Util.confirm({
            title: '',
            onOk: () => {
                deleteRelateTherapist({
                    station_id: this.station_id,
                    therapist_id
                }).then(() => {
                    message.success("")
                    this.getList()
                }).catch(err => {
                    message.warning(err)
                })
            }
        })

    }


    render() {

        const columns = [
            {
                title: '',
                dataIndex: 'index',
                render:(text,row,index)=>{
                    return `${(this.state.data.currentPage-1)*(this.state.data.pageSize)+(index+1)}`
                }
            },
            {
                title: '',
                dataIndex: 'name',
            },
            {
                title: '',
                dataIndex: 'phone',
            },
            {
                title: '',
                dataIndex: 'gender',
                render: (text) => {
                    return text === 'male' ? '' : ''
                }
            },
            {
                title: '',
                dataIndex: 'email',
            },
            {
                title: '',
                dataIndex: 'birthday',
            },
            {
                title: '',
                dataIndex: 'action',
                render: (text, row) => (
                    <Space size="middle">
                        <Button size={"small"} type={"primary"} danger
                                onClick={this.remove.bind(this, row.user_id)}></Button>
                    </Space>
                ),
            },
        ];


        return (
            <div>

                <TherapistList visible={this.state.visible} station_id={this.station_id} onClose={this.close}/>


                <Row>
                    <Col span={20}>
                        <h3>{this.station_name} / </h3>
                    </Col>
                    <Col span={2}>
                        <Button type={"primary"} onClick={this.open}></Button>
                    </Col>
                    <Col span={2}>
                        <Button type={"default"} onClick={this.back}></Button>
                    </Col>
                </Row>
                <Divider/>
                <Row>
                    <Col span={24}>
                        <Table dataSource={this.state.data.data} columns={columns} rowKey="phone"
                               pagination={false}/>
                        {
                            this.state.data.count > 0
                                ?
                                (<Pagination showQuickJumper total={this.state.data.count}
                                             pageSize={this.state.data.pageSize} current={this.state.data.currentPage}
                                             onChange={this.getList}/>)
                                :
                                (null)
                        }
                    </Col>
                </Row>
            </div>
        );
    }
}

export default Index;