import React, {Component} from 'react';

import {Form, Input, Button, Modal, Row, Col, Card, message} from 'antd';

import Util from "../../../assets/js/Util";

import {login,forgetPassword} from "../../../http/service";

import Role from "../../../assets/js/Role";

import store from "../../../store";

import './index.less'


class Index extends Component {

    constructor(props) {
        super(props);
        this.state={
            isShowForgetModal:false,
            phone:''
        }
    }

    register=()=>{
        this.props.history.push('/user/register');
    }


    goAfterLogin = () => {
        const role = store.getState().role;

        switch (role) {
            case Role.admin:
                this.props.history.push('/division/list');
                break;
            case Role.divisionManager:
                this.props.history.push('/station/list');
                break;
            case Role.caseManager:
                this.props.history.push('/room/list');
                break;
            case Role.therapist:
                this.props.history.push('/therapist/orderList');
                break;
        }

    }
    login = (form) => {


        if (!Util.isValidPhone(form.phone)) {
            message.warning('')
        } else {
            login(form).then((data) => {

                let userInfo = data.userInfo;
                let token = data.token;

                let user_id = userInfo.user_id;
                let user_name = userInfo.name;
                let role = userInfo.role;

                if (role === Role.client) {
                    message.error('')
                } else {
                    sessionStorage.token = data.token;
                    sessionStorage.user_id = user_id
                    sessionStorage.role = role

                    let batchArray=[{
                        type: 'login',
                        payload: true
                    }, {
                        type: 'user_id',
                        payload: user_id
                    }, {
                        type: 'user_name',
                        payload: user_name
                    }, {
                        type: 'role',
                        payload: role
                    }, {
                        type: 'token',
                        payload: token
                    }]

                    if(role===Role.caseManager){
                        batchArray.push({
                            type: 'station_id',
                            payload: userInfo.station_id
                        })
                        batchArray.push({
                            type: 'station_name',
                            payload: userInfo.station_name
                        })
                    }
                    store.batchDispatch(batchArray)

                    message.success("")

                    this.goAfterLogin()
                }


            }).catch(err => {
                message.error(err)
            })
        }


    }

    forgetPassword=()=>{
        let phone=this.state.phone;
        if(phone && Util.isValidPhone(phone)){

            forgetPassword( {
                phone
            }).then((data) => {

                this.setState({
                    isShowForgetModal:false
                })

                message.success(`${phone}${data.email}`)

            }).catch(err => {
                message.error(err)
            })

        }else{
            message.warning(``)
        }
    }

    render() {

        const layout = {
            labelCol: {
                span: 8,
            },
            wrapperCol: {
                span: 16,
            },
        };
        const tailLayout = {
            wrapperCol: {
                offset: 8,
                span: 16,
            },
        };

        return (
            <section>
                <Modal
                    title=""
                    okText=""
                    cancelText=""
                    visible={this.state.isShowForgetModal}
                    onOk={this.forgetPassword}
                    onCancel={()=>{
                        this.setState({
                            isShowForgetModal:false
                        })
                    }}
                >
                    <Form>
                        <Form.Item
                            label=""
                        >
                            <Input maxLength={11} value={this.state.phone} placeholder='' onChange={(e)=>{
                                this.setState({
                                    phone:e.target.value
                                })
                            }}/>
                        </Form.Item>


                    </Form>
                </Modal>

                <Row align={'middle'} className='login_wraper'>
                    <Col span={6} offset={9}>
                        <Card title="">
                            <Form
                                {...layout}
                                name="basic"
                                onFinish={this.login}
                            >
                                <Form.Item
                                    label=""
                                    name="phone"
                                    rules={[
                                        {
                                            required: true,
                                            message: '',
                                        },
                                    ]}
                                >
                                    <Input maxLength={11}/>
                                </Form.Item>

                                <Form.Item
                                    label=""
                                    name="password"
                                    rules={[
                                        {
                                            required: true,
                                            message: '',
                                        },
                                    ]}
                                >
                                    <Input.Password maxLength={30}/>
                                </Form.Item>

                                <div className='forget-password' onClick={()=>{
                                    this.setState({
                                        isShowForgetModal:true
                                    })
                                }}></div>

                                <Form.Item {...tailLayout}>
                                    <Button type="primary" htmlType="submit">
                                        
                                    </Button>
                                    <Button type="default" className='register-btn' onClick={this.register}>
                                        
                                    </Button>
                                </Form.Item>
                            </Form>
                        </Card>


                    </Col>
                </Row>
            </section>

        );
    }
}

export default Index

