import React, {Component} from 'react';
import {Form, Input, Row, Col, Button, Select, Radio} from "antd";
import Util from "../../../../../assets/js/Util";
import {PlusOutlined,DeleteFilled} from "@ant-design/icons";

class Wenda extends Component {
    constructor(props) {
        super(props);

        this.state = {
        }

    }

    componentDidMount() {
        if(this.props.isEdit){
            setTimeout(()=>{
                let data=this.props.data;
                this.props.form.current.setFieldsValue({
                    name:data.name,
                    lieRule:data.lieRule,
                    answer:data.answer
                })
            },1)
        }else{
            setTimeout(()=>{
                this.props.form.current.setFieldsValue({
                    answer:[{}]
                })
            },1)
        }
    }


    render() {
        return (
            <React.Fragment>

                <Form.Item name={'name'} label="" rules={[
                    {
                        required: true,
                        message: '!',
                    },
                ]}>
                    <Input.TextArea rows={2} placeholder={''}></Input.TextArea>
                </Form.Item>

                <Form.Item name="lieRule" label=""  rules={[
                    {
                        required: true,
                        message: '!',
                    },
                ]}>
                    <Radio.Group>
                        <Radio value={0}></Radio>
                        <Radio value={1}></Radio>
                    </Radio.Group>
                </Form.Item>


                <Row style={{marginTop: "10px", fontSize: "1em", fontWeight: "bold"}}>
                    <Col span="10" offset={6}>
                        
                    </Col>
                    <Col span="4" offset="1">
                        
                    </Col>
                    <Col span="1" offset="1">
                        
                    </Col>
                </Row>

                <Form.List name="answer">
                    {(fields, {add, remove}) => {
                        return (
                            <div>

                                {fields.map((field, index) => (
                                    <Row key={index}>


                                        <Col span="10" offset={6}>
                                            <Form.Item
                                                {...field}
                                                fieldKey={[field.fieldKey, 'key']}
                                                name={[field.name, 'key']} rules={[
                                                {
                                                    required: true,
                                                    message: '!',
                                                },
                                            ]}>
                                                <Input maxLength={200} placeholder={''}></Input>
                                            </Form.Item>
                                        </Col>
                                        <Col span={4} offset={1}>
                                            <Form.Item
                                                {...field}
                                                name={[field.name, 'value']}
                                                fieldKey={[field.fieldKey, 'value']}
                                                rules={[{required: true, message: '!'}]}>
                                                <Select
                                                    placeholder=""
                                                    value={field.value}
                                                >
                                                    {
                                                        Util.questionScoreArray.map((item, index) => {
                                                            return <Select.Option key={item}
                                                                                  value={item}>{item}</Select.Option>
                                                        })
                                                    }

                                                </Select>
                                            </Form.Item>
                                        </Col>
                                        <Col span={1} offset={1}>
                                            {
                                                fields.length>1?
                                                    <DeleteFilled style={{fontSize:'1.2em',marginTop:'0.4em'}}
                                                        onClick={() => {
                                                            remove(field.name);
                                                        }}
                                                    />
                                                    :
                                                    null
                                            }
                                        </Col>
                                    </Row>
                                ))}

                                <Row>
                                    <Col span={18} offset={6}>
                                        <Form.Item>
                                            <Button
                                                type="dashed"
                                                onClick={() => {
                                                    add();
                                                }}
                                                block
                                            >
                                                <PlusOutlined/> 
                                            </Button>
                                        </Form.Item>
                                    </Col>
                                </Row>
                            </div>
                        );
                    }}
                </Form.List>


            </React.Fragment>

        );
    }
}

export default Wenda;
