import React, {Component} from 'react';

import {Row, Col, Button, Table, Space, message, Modal, Divider, Pagination} from "antd";

import Util from "../../../assets/js/Util";

import Role from "../../../assets/js/Role";

import {getUserList,deleteUser} from "../../../http/service";

class Index extends Component {

    constructor() {
        super();
        this.state={
            data:{
                data:[]
            }
        }
    }

    componentDidMount() {
        this.getList(1)
    }


    getList=(page)=> {


    }
    detail=(row)=>{

        this.props.history.push({
            pathname:'/preCheck/operate',
            state:{
                opType:'edit',
                formItem:row
            }
        })
    }




    render() {

        const columns = [
            {
                title: '',
                dataIndex: 'index',
                render:(text,row,index)=>{
                    return `${(this.state.data.currentPage-1)*(this.state.data.pageSize)+(index+1)}`
                }
            },
            {
                title: '',
                dataIndex: 'name',
            },
            {
                title: '',
                dataIndex: 'phone',
            },
            {
                title: '',
                dataIndex: 'gender',
                render:(text)=>{
                    return text==='male'?'':''
                }
            },
            {
                title: '',
                dataIndex: 'answerDate',
            },
            {
                title: '',
                dataIndex: 'state',
                render:(text)=>{
                    return text==='0'?'':''
                }
            },
            {
                title: '',
                dataIndex: 'action',
                render: (text, row) => (
                    <Space size="middle">
                        <Button type={"primary"} onClick={this.detail.bind(this,row)}></Button>
                    </Space>
                ),
            },
        ];

        return (
            <div>
                <Row>
                    <Col span={22}>
                        <h3></h3>
                    </Col>
                </Row>
                <Divider/>
                <Row>
                    <Col span={24}>
                        <Table dataSource={this.state.data.data} columns={columns} rowKey="user_id" pagination={false}/>
                        {
                            this.state.data.count > 0
                                ?
                                (<Pagination showQuickJumper total={this.state.data.count} pageSize={this.state.data.pageSize} current={this.state.data.currentPage} onChange={this.getList}/>)
                                :
                                (null)
                        }
                    </Col>
                </Row>
            </div>
        );
    }
}

export default Index;