import React, {Component} from 'react';
import {Button, Col, Divider, Pagination, Row, Space, Table} from "antd";
import Util from "../../../assets/js/Util";
import {getBlackList,deleteBlacklist} from "../../../http/service";


class Index extends Component {

    constructor(props) {
        super(props);

        this.state = {
            data: [],

        }
    }

    componentDidMount() {
        this.getList(1)
    }


    getList = (page) => {
        page=page||1;

        getBlackList({
            page,
            pageSize:Util.pageSize
        }).then(data=>{
            this.setState({
                data: data,
                count: data.length
            })
        })

    }


    delete = (blacklist_id) => {


        Util.confirm({
            title: '',
            content: '',
            onOk: () => {

                deleteBlacklist({
                    blacklist_id
                }).then(()=>{
                    Util.success("")
                    this.getList()
                }).catch(err=>{
                    Util.error(err)
                })


            },
            onCancel: () => {
            }
        });


    }


    render() {

        const columns = [
            {
                title: '',
                dataIndex: 'index',
                render:(text,row,index)=>{
                    return `${(this.state.data.currentPage-1)*(this.state.data.pageSize)+(index+1)}`
                }
            },
            {
                title: '',
                dataIndex: 'name',
            },
            {
                title: '',
                dataIndex: 'phone',
            },
            {
                title: '',
                dataIndex: 'add_date',
            },
            {
                title: '',
                dataIndex: 'action',
                render: (text, row) => (
                    <Button size={"small"} type={"primary"} danger
                            onClick={this.delete.bind(this, row.blacklist_id)}></Button>

                ),
            },
        ];


        return (
            <div>
                <Row>
                    <Col span={22}>
                        <h3></h3>
                    </Col>
                </Row>
                <Divider/>
                <Row justify={'center'}>
                    <Col span={24}>
                        <Table dataSource={this.state.data.data} columns={columns} rowKey="user_id" pagination={false}/>
                        {
                            this.state.data.count > 0
                                ?
                                (<Pagination showQuickJumper total={this.state.data.count} onChange={this.getList}/>)
                                :
                                (null)
                        }
                    </Col>
                </Row>
            </div>
        );
    }
}

export default Index;