import React, {Component} from 'react';
import {Button, Col, Row, Form, Input, Select, Space, message, Divider, DatePicker, Card} from "antd";
import {register} from "../../../http/service";
import Util from "../../../assets/js/Util";
import moment from 'moment'
import './index.less'

const {Option} = Select;

class Index extends Component {

    constructor(props) {
        super(props);


        this.state = {
            formItem: {}
        }

    }


    back = () => {
        this.props.history.push('/user/login')
    }
    operate = (form) => {

        form.birthday = moment(form.birthday).format('yyyy-MM-DD');

        this.setState({
            formItem: Object.assign(this.state.formItem, form)
        })


        if (!Util.isValidPhone(this.state.formItem.phone)) {
            Util.warning("")
            return;
        }

        if (!Util.isValidEmail(this.state.formItem.email)) {
            Util.warning("")
            return;
        }

        if (!Util.isValidID(this.state.formItem.identification_no)) {
            Util.warning("")
            return;
        }

        register(this.state.formItem).then((data) => {
            Util.success("")

            this.back()

        }).catch(err => {
            Util.error(err)
        })


    }

    render() {


        return (
            <div>
                <Row align={'middle'} className='login_wraper'>
                    <Col span={8} offset={8}>
                        <Card title="">
                            <Form
                                layout="horizontal"
                                labelCol={{span: 4}}
                                wrapperCol={{span: 20}}
                                initialValues={this.state.formItem}
                                onFinish={this.operate}
                            >
                                <Form.Item
                                    label=""
                                    name="phone"
                                    rules={[
                                        {
                                            required: true,
                                            message: '!',
                                        },
                                    ]}
                                >
                                    <Input placeholder={''} maxLength={11}/>
                                </Form.Item>

                                <Form.Item
                                    label=""
                                    name="identification_no"
                                    rules={[
                                        {
                                            required: true,
                                            message: '!',
                                        },
                                    ]}
                                >
                                    <Input placeholder={''} maxLength={21}/>
                                </Form.Item>

                                <Form.Item
                                    label=""
                                    name="password"
                                    rules={[
                                        {
                                            required: true,
                                            message: '!',
                                        },
                                    ]}
                                >
                                    <Input.Password placeholder={''} maxLength={20}/>
                                </Form.Item>

                                <Form.Item
                                    label=""
                                    name="name"
                                    rules={[
                                        {
                                            required: true,
                                            message: '!',
                                        },
                                    ]}
                                >
                                    <Input placeholder={''} maxLength={20}/>
                                </Form.Item>


                                <Form.Item name="gender" label=""
                                           rules={[{required: true, message: '!'}]}>
                                    <Select
                                        placeholder=""
                                        value={this.state.formItem.gender}
                                    >
                                        <Option key={1} value='male'></Option>
                                        <Option key={2} value='female'></Option>

                                    </Select>
                                </Form.Item>

                                <Form.Item
                                    label=""
                                    name="email"
                                    rules={[
                                        {
                                            required: true,
                                            message: '!',
                                        },
                                    ]}
                                >
                                    <Input placeholder={''} maxLength={50}/>
                                </Form.Item>

                                <Form.Item
                                    label=""
                                    name="birthday"
                                    format="YYYY-MM-DD"
                                    rules={[
                                        {
                                            required: true,
                                            message: '!',
                                        },
                                    ]}
                                >
                                    <DatePicker/>
                                </Form.Item>


                                <Form.Item wrapperCol={{offset: 8, span: 16}}>


                                    <Space>
                                        <Button style={{width: '6em'}} type="default" onClick={this.back}>
                                            
                                        </Button>
                                        <Button style={{width: '6em'}} type="primary" htmlType="submit">
                                            
                                        </Button>
                                    </Space>
                                </Form.Item>
                            </Form>
                        </Card>
                    </Col>
                </Row>
            </div>
        );
    }
}

export default Index;