import React, {Component} from 'react';
import {Form, Input, Row, Col, Button, Select,Checkbox} from "antd";
import Util from "../../../../../assets/js/Util";
import {PlusOutlined,DeleteFilled} from "@ant-design/icons";

class Wenda extends Component {
    constructor(props) {
        super(props);

        this.state = {
            isEdit: false,
            tiaomu: {
                answer: []
            }
        }

    }

    componentDidMount() {
        if(this.props.isEdit){
            setTimeout(()=>{
                let data=this.props.data;
                debugger
                data.children.forEach(item=>{
                    item.isReverse=item.isReverse===1?'checked':''
                })
                this.props.form.current.setFieldsValue({
                    name:data.name,
                    answer:data.answer,
                    children:data.children
                })
            },1)
        }else{
            setTimeout(()=>{
                this.props.form.current.setFieldsValue({
                    answer:[{}],
                    children:[{
                    }]
                })
            },1)
        }
    }

    render() {
        return (
            <React.Fragment>

                <Form.Item name={'name'} label="" rules={[
                    {
                        required: true,
                        message: '!',
                    },
                ]}>
                    <Input.TextArea rows={5} placeholder={''}></Input.TextArea>
                </Form.Item>

                <Row style={{marginTop: "10px", fontSize: "1em", fontWeight: "bold"}}>
                    <Col span="16">
                        
                    </Col>
                    <Col span="4" offset="1">
                        
                    </Col>
                    <Col span="2" offset="1">
                        
                    </Col>
                </Row>

                <Form.List name="children">
                    {(fields, {add, remove}) => {
                        return (
                            <div>

                                {fields.map((field, index) => (
                                    <Row key={index}>


                                        <Col span="16">
                                            <Form.Item
                                                {...field}
                                                fieldKey={[field.fieldKey, 'name']}
                                                name={[field.name, 'name']} rules={[
                                                {
                                                    required: true,
                                                    message: '!',
                                                },
                                            ]}>
                                                <Input maxLength={200} placeholder={''}></Input>
                                            </Form.Item>
                                        </Col>
                                        <Col span={4} offset={1}>
                                            <Form.Item {...field} valuePropName="checked"
                                                       name={[field.name, 'isReverse']}
                                                       fieldKey={[field.fieldKey, 'isReverse']}>
                                                <Checkbox />
                                            </Form.Item>

                                        </Col>
                                        <Col span={2} offset={1}>
                                            {
                                                fields.length>1?
                                                    <DeleteFilled  style={{fontSize:'1.2em',marginTop:'0.4em'}}
                                                        onClick={() => {
                                                            remove(field.name);
                                                        }}
                                                    />
                                                    :
                                                    null
                                            }
                                        </Col>
                                    </Row>
                                ))}

                                <Form.Item>
                                    <Button
                                        type="dashed"
                                        onClick={() => {
                                            add();
                                        }}
                                        block
                                    >
                                        <PlusOutlined/> 
                                    </Button>
                                </Form.Item>
                            </div>
                        );
                    }}
                </Form.List>

                <Row style={{marginTop: "10px", fontSize: "1em", fontWeight: "bold"}}>
                    <Col span="16">
                        
                    </Col>
                    <Col span="4" offset="1">
                        
                    </Col>
                    <Col span="2" offset="1">
                        
                    </Col>
                </Row>

                <Form.List name="answer">
                    {(fields, {add, remove}) => {
                        return (
                            <div>

                                {fields.map((field, index) => (
                                    <Row key={index}>


                                        <Col span="16">
                                            <Form.Item
                                                {...field}
                                                fieldKey={[field.fieldKey, 'key']}
                                                name={[field.name, 'key']} rules={[
                                                {
                                                    required: true,
                                                    message: '!',
                                                },
                                            ]}>
                                                <Input maxLength={200} placeholder={''}></Input>
                                            </Form.Item>
                                        </Col>
                                        <Col span={4} offset={1}>
                                            <Form.Item
                                                {...field}
                                                name={[field.name, 'value']}
                                                fieldKey={[field.fieldKey, 'value']}
                                                rules={[{required: true, message: '!'}]}>
                                                <Select
                                                    placeholder=""
                                                    value={field.value}
                                                >
                                                    {
                                                        Util.questionScoreArray.map((item, index) => {
                                                            return <Select.Option key={item}
                                                                                  value={item}>{item}</Select.Option>
                                                        })
                                                    }

                                                </Select>
                                            </Form.Item>
                                        </Col>
                                        <Col span={2} offset={1}>
                                            {
                                                fields.length>1?
                                                    <DeleteFilled style={{fontSize:'1.2em',marginTop:'0.4em'}}
                                                        onClick={() => {
                                                            remove(field.name);
                                                        }}
                                                    />
                                                    :
                                                    null
                                            }
                                        </Col>
                                    </Row>
                                ))}

                                <Form.Item>
                                    <Button
                                        type="dashed"
                                        onClick={() => {
                                            add();
                                        }}
                                        block
                                    >
                                        <PlusOutlined/> 
                                    </Button>
                                </Form.Item>
                            </div>
                        );
                    }}
                </Form.List>


            </React.Fragment>

        );
    }
}

export default Wenda;
