function getOptions(){
    let options=[]

    let province=pcz['86'];

    for(let key in province){
        options.push({
            value:key,
            label:province[key],
            children:[]
        })

        let city=pcz[key]
        for(let key in city){
            options[options.length-1].children.push({
                value:key,
                label:city[key]
            })
        }

    }
    return options;
}

let pczOptions=getOptions();



export {pczOptions}