import React, {Component} from 'react';
import {Button, Col, Row, Form, Input, Select, Space, message, Divider, DatePicker, Card} from "antd";
import {addStation,updateStation} from "../../../http/service";
import Util from "../../../assets/js/Util";
import moment from 'moment'
import Role from "../../../assets/js/Role";
import './index.less'
const {Option} = Select;

class Index extends Component {

    formRef = React.createRef();

    constructor(props) {
        super(props);

        this.isEdit = this.props.location.state && this.props.location.state.opType === 'edit'


        this.state = {
            formItem: this.isEdit?this.props.location.state.formItem:{}
        }

    }

    componentDidMount() {
        this.formRef.current.setFieldsValue(this.state.formItem)
    }

    back=()=>{
        this.props.history.goBack();
    }




    operate = (form) => {

        this.setState({
            formItem: Object.assign(this.state.formItem, form)
        })


        if (!Util.isValidPhone(this.state.formItem.phone)) {
            Util.warning("")
            return;
        }



        if(this.isEdit){
            updateStation(this.state.formItem).then((data) => {
                Util.success("")

                this.back()

            }).catch(err => {
                Util.error(err)
            })
        }else{
            addStation(this.state.formItem).then((data) => {
                Util.success("")

                this.back()

            }).catch(err => {
                Util.error(err)
            })
        }




    }

    render() {


        return (
            <div>
                <Row>
                    <Col span={22}>
                        <h3></h3>
                    </Col>
                </Row>
                <Divider/>
                <Row>
                    <Col span={8} offset={8}>
                        <Form
                            ref={this.formRef}
                            layout="horizontal"
                            labelCol={{span: 4}}
                            wrapperCol={{span: 20}}
                            onFinish={this.operate}
                        >

                            <Form.Item
                                label=""
                                name="station_name"
                                rules={[
                                    {
                                        required: true,
                                        message: '!',
                                    },
                                ]}
                            >
                                <Input placeholder={''} maxLength={20}/>
                            </Form.Item>

                            <Form.Item
                                label=""
                                name="address"
                                rules={[
                                    {
                                        required: true,
                                        message: '!',
                                    },
                                ]}
                            >
                                <Input key={1} placeholder={''} maxLength={11}/>
                            </Form.Item>

                            <Form.Item
                                label=""
                                name="phone"
                                rules={[
                                    {
                                        required: true,
                                        message: '!',
                                    },
                                ]}
                            >
                                <Input key={1} placeholder={''} maxLength={11}/>
                            </Form.Item>


                            <Form.Item wrapperCol={{offset: 8, span: 16}}>


                                <Space>
                                    <Button style={{width: '6em'}} type="default" onClick={this.back} >
                                        
                                    </Button>
                                    <Button style={{width: '6em'}} type="primary" htmlType="submit">
                                        
                                    </Button>
                                </Space>
                            </Form.Item>
                        </Form>
                    </Col>
                </Row>
            </div>
        );
    }
}

export default Index;