import React, {Component} from 'react';
import {Button, Col, Row, Form, Input, Select, Space, message, Divider, DatePicker} from "antd";
import {addDivisionAdmin, updateDivisionAdmin} from "../../../http/service";
import Util from "../../../assets/js/Util";
import Role from "../../../assets/js/Role";
import moment from 'moment'

const {Option} = Select;

class Index extends Component {

    constructor(props) {
        super(props);

        this.isEdit = this.props.location.state && this.props.location.state.opType === 'edit'
        this.division_id = (this.props.location.state && this.props.location.state.division_id) ? this.props.location.state.division_id : ''

        let formItem={}
        if(this.isEdit){
            formItem=this.props.location.state.formItem
            formItem.birthday=moment(formItem.birthday)
        }

        
        this.state = {
            formItem
        }

    }


    back = () => {
        this.props.history.goBack()
    }
    operate = (form) => {

        form.birthday=moment(form.birthday).format('yyyy-MM-DD');
        form.division_id=this.division_id;

        this.setState({
            formItem:Object.assign(this.state.formItem,form)
        })

        if (!Util.isValidPhone(this.state.formItem.phone)) {
            Util.warning('')
            return;
        }

        if (!Util.isValidEmail(this.state.formItem.email)) {
            Util.warning('')
            return;
        }

        if (this.isEdit) {
            updateDivisionAdmin(this.state.formItem).then((data) => {

                Util.success("")

                this.props.history.push({
                    pathname: '/division/adminList',
                    state: {
                        division_id: this.division_id
                    }
                })


            }).catch(err => {
                Util.error(err)
            })
        } else {
            addDivisionAdmin(this.state.formItem).then((data) => {

                Util.success("")

                this.props.history.push({
                    pathname: '/division/adminList',
                    state: {
                        division_id: this.division_id
                    }
                })


            }).catch(err => {
                Util.error(err)
            })
        }


    }

    render() {




        return (
            <div>
                <Row>
                    <Col span={22}>
                        <h3></h3>
                    </Col>
                </Row>
                <Divider/>
                <Row>
                    <Col offset={8} span={8}>

                        <Form
                            layout="horizontal"
                            labelCol={{span:4}}
                            wrapperCol={{ span: 20 }}
                            initialValues={this.state.formItem}
                            onFinish={this.operate}
                        >
                            <Form.Item
                                label=""
                                name="phone"
                                rules={[
                                    {
                                        required: true,
                                        message: '!',
                                    },
                                ]}
                            >
                                <Input placeholder={''} maxLength={11}/>
                            </Form.Item>

                            <Form.Item
                                label=""
                                name="name"
                                rules={[
                                    {
                                        required: true,
                                        message: '!',
                                    },
                                ]}
                            >
                                <Input placeholder={''} maxLength={20}/>
                            </Form.Item>


                            <Form.Item name="gender" label=""
                                       rules={[{required: true, message: '!'}]}>
                                <Select
                                    placeholder=""
                                    value={this.state.formItem.gender}
                                >
                                    <Option key={1} value='male'></Option>
                                    <Option key={2} value='female'></Option>

                                </Select>
                            </Form.Item>

                            <Form.Item
                                label=""
                                name="email"
                                rules={[
                                    {
                                        required: true,
                                        message: '!',
                                    },
                                ]}
                            >
                                <Input placeholder={''} maxLength={50}/>
                            </Form.Item>

                            <Form.Item
                                label=""
                                name="birthday"
                                format="YYYY-MM-DD"
                                rules={[
                                    {
                                        required: true,
                                        message: '!',
                                    },
                                ]}
                            >
                                <DatePicker/>
                            </Form.Item>


                            <Form.Item wrapperCol={{offset: 8, span: 16}}>


                                <Space>
                                    <Button type="default" onClick={this.back}>
                                        
                                    </Button>
                                    <Button type="primary" htmlType="submit">
                                        
                                    </Button>
                                </Space>
                            </Form.Item>
                        </Form>
                    </Col>
                </Row>
            </div>
        );
    }
}

export default Index;