import React, {Component} from 'react';
import {Button, Col, Row, Form, Input, Select, Space, message, Divider, DatePicker, Radio} from "antd";
import {addMeasure, updateMeasure} from "../../../http/service";

const {Option} = Select;
const {TextArea} = Input;

class Index extends Component {

    constructor(props) {
        super(props);

        this.isEdit = this.props.location.state && this.props.location.state.opType === 'edit'
        this.from = (this.props.location.state && this.props.location.state.from ) ?this.props.location.state.from: '/measure/list'

        let obj=this.isEdit ? this.props.location.state.formItem : {
            chartType:null
        }
        this.state = {
            formItem: obj
        }


    }


    componentDidMount() {

    }


    back = () => {
        this.props.history.push(this.from)
    }

    operate = (values) => {

        this.setState({
            formItem: Object.assign(this.state.formItem, values)
        })

        if (this.isEdit) {

            updateMeasure(this.state.formItem).then((data) => {

                message.success("")
                this.props.history.push('/measure/list')

            }).catch(err => {
                message.error(err)
            })
        } else {

            addMeasure(this.state.formItem).then((data) => {

                message.success("")
                this.props.history.push('/measure/list')

            }).catch(err => {
                message.error(err)
            })
        }


    }

    render() {

        return (
            <div>
                <Row>
                    <Col span={22}>
                        <h3></h3>
                    </Col>
                </Row>
                <Divider/>
                <Row>
                    <Col offset={8} span={8}>

                        <Form
                            name="basic"
                            labelCol={{span: 6}}
                            initialValues={this.state.formItem}
                            onFinish={this.operate}
                        >
                            <Form.Item
                                label=""
                                name="name"
                                rules={[
                                    {
                                        required: true,
                                        message: '!',
                                    },
                                ]}
                            >
                                <Input placeholder={''}/>
                            </Form.Item>

                            <Form.Item
                                label=""
                                name="alias"
                                rules={[
                                    {
                                        required: true,
                                        message: '!',
                                    },
                                ]}
                            >
                                <Input placeholder={''}/>
                            </Form.Item>

                            <Form.Item
                                label=""
                                name="description"
                                rules={[
                                    {
                                        required: true,
                                        message: '!',
                                    },
                                ]}
                            >
                                <TextArea rows={3} placeholder={''}/>
                            </Form.Item>

                            <Form.Item
                                label=""
                                name="start_description"
                                rules={[
                                    {
                                        required: true,
                                        message: '!',
                                    },
                                ]}
                            >
                                <TextArea rows={3} placeholder={''}/>
                            </Form.Item>

                            <Form.Item name="chartType" label=""
                                       >
                                <Select
                                    placeholder=""
                                    value={this.state.formItem.chartType}
                                >
                                    <Option value={null}></Option>
                                    <Option value="line"></Option>
                                    <Option value="bar"></Option>
                                    <Option value="pie"></Option>
                                </Select>
                            </Form.Item>

                            <Form.Item wrapperCol={{offset: 8, span: 16}}>
                                <Space>
                                    <Button type="default" onClick={this.back}>
                                        
                                    </Button>
                                    <Button type="primary" htmlType="submit">
                                        
                                    </Button>
                                </Space>
                            </Form.Item>
                        </Form>
                    </Col>
                </Row>
            </div>
        );
    }
}

export default Index;