import React, {Component} from 'react';
import {Form, Input, Row, Col, Button, Select, Checkbox, Radio, Upload} from "antd";
import Util from "../../../../../assets/js/Util";
import {upLoadFile} from "../../../../../http/service";
import {DeleteFilled, PlusOutlined, UploadOutlined} from '@ant-design/icons';

class Wenda extends Component {
    constructor(props) {
        super(props);
        this.form = this.props.form;
        this.suffixArrayOfMusic = ["mp3", "wave"]
        this.suffixArrayOfPicture = ["jpg", "jpeg", "png"]
        this.state = {
        }

    }


    componentDidMount() {
        if(this.props.isEdit){
            setTimeout(()=>{
                let data=this.props.data;
                this.props.form.current.setFieldsValue({
                    name:data.name,
                    mediaType:'picture',
                    children:data.children
                })
            },1)
        }else{
            setTimeout(()=>{
                this.props.form.current.setFieldsValue({
                    mediaType:'picture',
                    children:[{
                        answer:[{}]
                    }]
                })
            },1)
        }
    }


    beforeUpload = (file) => {
        let size = file / 1000 / 1000;//M

        let suffix = file.name.split('.')

        suffix = suffix[suffix.length - 1]

        if (size > 10) {
            Util.warning("10M!")
            this.file = null;
            return false;
        }

        let mediaType = this.form.current.getFieldValue("mediaType")

        if (mediaType === 'music') {
            if (this.suffixArrayOfMusic.indexOf(suffix) === -1) {
                Util.warning(`${this.suffixArrayOfMusic}!`)
                this.file = null;
                return false;
            }
        } else {
            if (this.suffixArrayOfPicture.indexOf(suffix) === -1) {
                Util.warning(`${this.suffixArrayOfPicture}!`)
                this.file = null;
                return false;
            }
        }

        let formData = new FormData()

        formData.append('file', file)

        upLoadFile(formData).then(data => {

            this.setState({
                file
            })

            let url = data.url

            this.form.current.setFieldsValue({
                url
            })

        }).catch(error => {
            Util.error(error)
        })

        return false;
    }

    render() {
        return (
            <React.Fragment>

                <Form.Item name={'name'} label="" rules={[
                    {
                        required: true,
                        message: '!',
                    },
                ]}>
                    <Input.TextArea rows={5} placeholder={''}></Input.TextArea>
                </Form.Item>

                <Form.Item
                    name="mediaType"
                    label=""
                    rules={[
                    {
                        required: true,
                        message: '!',
                    },
                ]}>
                    <Radio.Group >
                        <Radio value="music"></Radio>
                        <Radio value="picture"></Radio>
                    </Radio.Group>
                </Form.Item>

                <Form.Item label=''>
                    <Upload
                        showUploadList={false}
                        beforeUpload={this.beforeUpload}
                    >
                        <Button>
                            <UploadOutlined/> 
                        </Button>
                    </Upload>
                    {
                        this.state.file ?
                            <div>: {this.state.file.name}</div>
                            :
                            null
                    }
                </Form.Item>

                <Form.Item name={'url'} style={{display:'none'}}>
                    <Input></Input>
                </Form.Item>


                <Row style={{marginTop: "10px", fontSize: "1em", fontWeight: "bold"}}>
                    <Col span={15} offset={6}>
                        
                    </Col>
                    <Col span="2" offset="1">
                        
                    </Col>
                </Row>

                <Form.List name="children">
                    {(fields, {add, remove}) => {
                        return (
                            <div>

                                {fields.map((field, index) => (
                                    <Row key={index} >

                                        <Col span={6}></Col>

                                        <Col span={18}>

                                            <Row >

                                                <Col span={20} >
                                                    <Form.Item
                                                        {...field}
                                                        fieldKey={[field.fieldKey, 'name']}
                                                        name={[field.name, 'name']} rules={[
                                                        {
                                                            required: true,
                                                            message: '!',
                                                        },
                                                    ]}>
                                                        <Input maxLength={200} placeholder={''}></Input>


                                                    </Form.Item>


                                                    <Row style={{marginTop: "10px", fontSize: "1em", fontWeight: "bold"}}>
                                                        <Col span="15">
                                                            
                                                        </Col>
                                                        <Col span="5" offset="1">
                                                            
                                                        </Col>
                                                        <Col span="2" offset="1">
                                                            
                                                        </Col>
                                                    </Row>
                                                    <Form.List
                                                        fieldKey={[field.fieldKey, 'answer']}
                                                        name={[field.name, 'answer']}>
                                                        {(fields, {add, remove}) => {
                                                            return (
                                                                <div>

                                                                    {fields.map((field, index) => (
                                                                        <Row key={index}>


                                                                            <Col span="15">
                                                                                <Form.Item
                                                                                    {...field}
                                                                                    fieldKey={[field.fieldKey, 'key']}
                                                                                    name={[field.key, 'key']} rules={[
                                                                                    {
                                                                                        required: true,
                                                                                        message: '!',
                                                                                    },
                                                                                ]}>
                                                                                    <Input maxLength={200}
                                                                                           placeholder={''}></Input>
                                                                                </Form.Item>
                                                                            </Col>
                                                                            <Col span={5} offset={1}>
                                                                                <Form.Item
                                                                                    name={[field.name, 'value']}
                                                                                    fieldKey={[field.fieldKey, 'value']}
                                                                                    rules={[{
                                                                                        required: true,
                                                                                        message: '!'
                                                                                    }]}>
                                                                                    <Select
                                                                                        placeholder=""
                                                                                        value={field.value}
                                                                                    >
                                                                                        {
                                                                                            Util.questionScoreArray.map((item, index) => {
                                                                                                return <Select.Option key={item}
                                                                                                                      value={item}>{item}</Select.Option>
                                                                                            })
                                                                                        }

                                                                                    </Select>
                                                                                </Form.Item>
                                                                            </Col>
                                                                            <Col span={2} offset={1} style={{textAlign:'center'}}>
                                                                                {
                                                                                    fields.length>1?
                                                                                        <DeleteFilled  style={{fontSize:'1.2em',marginTop:'0.4em'}}
                                                                                                       onClick={() => {
                                                                                                           remove(field.name);
                                                                                                       }}
                                                                                        />
                                                                                        :
                                                                                        null
                                                                                }
                                                                            </Col>
                                                                        </Row>
                                                                    ))}

                                                                    <Form.Item>
                                                                        <Button
                                                                            type="dashed"
                                                                            onClick={() => {
                                                                                add({
                                                                                    answer:[{},{}]
                                                                                });
                                                                            }}
                                                                            key={Math.random()}
                                                                            block
                                                                        >
                                                                            <PlusOutlined/> 
                                                                        </Button>
                                                                    </Form.Item>
                                                                </div>
                                                            );
                                                        }}
                                                    </Form.List>

                                                </Col>
                                                <Col span={2} offset={1} style={{textAlign:'center'}}>
                                                    {
                                                        fields.length>1?
                                                            <DeleteFilled  style={{fontSize:'1.2em',marginTop:'0.4em'}}
                                                                           onClick={() => {
                                                                               remove(field.name);
                                                                           }}
                                                            />
                                                            :
                                                            null
                                                    }

                                                </Col>
                                            </Row>

                                        </Col>


                                    </Row>
                                ))}

                                <Row>
                                    <Col span={15} offset={6}>
                                        <Form.Item>
                                            <Button
                                                type="dashed"
                                                onClick={() => {
                                                    add({
                                                        answer:[{}]
                                                    });
                                                }}
                                                block
                                            >
                                                <PlusOutlined/> 
                                            </Button>
                                        </Form.Item>
                                    </Col>
                                </Row>
                            </div>
                        );
                    }}
                </Form.List>


            </React.Fragment>

        );
    }
}

export default Wenda;
