import React, {Component} from 'react';

import {Row, Col, Button, Table, Space, message, Modal, Divider, Pagination} from "antd";

import Util from "../../../assets/js/Util";

import Role from "../../../assets/js/Role";

import {deleteRoom,getRoomList,onOffRoom} from "../../../http/service";

class Index extends Component {

    constructor(props) {
        super(props);
        this.state={
            data:{
                data:[]
            }
        }
    }

    componentDidMount() {
        this.getList(1)
    }


    getList=(page)=> {

        page=page||1;

        let pageSize=Util.pageSize
        pageSize=2;

        getRoomList( {
            page,
            pageSize,
        }).then((data) => {

            this.setState({
                data:data
            })

        }).catch(err => {
            message.warning(err)
        })
    }
    add=()=> {
        this.props.history.push({
            pathname:'/room/operate',
            state:{
                // station_id:this.station_id
            }
        })
    }
    edit=(row)=>{

        this.props.history.push({
            pathname:'/room/operate',
            state:{
                opType:'edit',
                formItem:row,
            }
        })
    }

    occupy=(row)=> {
        this.props.history.push({
            pathname:'/room/occupy',
            state:{
                room_id:row.room_id,
                name:row.name
            }
        })
    }

    go2UseablePeriodSet=()=>{
        this.props.history.push({
            pathname:'/room/periodSet',
            state:{
            }
        })
    }

    onOff=(row)=> {

        onOffRoom(row).then(() => {

            Util.success("")
            this.getList(this.state.data.currentPage)


        }).catch(err => {
            Util.error(err)
        })


    }
    delete=(room_id)=>{
        Util.confirm({
            title:'',
            onOk:()=>{
                deleteRoom({
                    room_id
                }).then(()=>{
                    message.success("")
                    this.getList()
                }).catch(err=>{
                    message.warning(err)
                })
            }
        })

    }



    render() {

        const columns = [
            {
                title: '',
                dataIndex: 'index',
                render:(text,row,index)=>{
                    return `${(this.state.data.currentPage-1)*(this.state.data.pageSize)+(index+1)}`
                }
            },
            {
                title: '',
                dataIndex: 'name',
            },
            {
                title: '',
                dataIndex: 'position',
            },
            {
                title: '',
                dataIndex: 'state',
                render:(text)=>{
                    return text===0?'':''
                }
            },
            {
                title: '',
                dataIndex: 'action',
                render: (text, row) => (
                    <Space size="middle">
                        <Button size={"small"} type={"primary"} onClick={this.edit.bind(this,row)}></Button>
                        <Button size={"small"} type={"primary"} danger onClick={this.delete.bind(this,row.room_id)}></Button>
                        <Button size={"small"} type={"primary"} onClick={this.occupy.bind(this,row)}></Button>
                        {
                            row.state===0?
                                (
                                    <Button size={"small"} type={"primary"} danger onClick={this.onOff.bind(this,row)}></Button>
                                )
                                :
                                (
                                    <Button size={"small"} type={"primary"} onClick={this.onOff.bind(this,row)}></Button>
                                )
                        }
                    </Space>
                ),
            },
        ];

        return (
            <div>
                <Row>
                    <Col span={20}>
                        <h3></h3>
                    </Col>
                    <Col span={2}>
                        <Button type={"primary"} onClick={this.add}></Button>
                    </Col>
                    <Col span={2}>
                        <Button type={"primary"} onClick={this.go2UseablePeriodSet}></Button>
                    </Col>
                </Row>
                <Divider/>
                <Row>
                    <Col span={24}>
                        <Table dataSource={this.state.data.data} columns={columns} rowKey="room_id" pagination={false}/>
                        {
                            this.state.data.count > 0
                                ?
                                (<Pagination showQuickJumper total={this.state.data.count} pageSize={this.state.data.pageSize} current={this.state.data.currentPage} onChange={this.getList}/>)
                                :
                                (null)
                        }
                    </Col>
                </Row>
            </div>
        );
    }
}

export default Index;