import React, {Component} from 'react';

import {Row,Col,Button,Table,Space,message,Modal,Divider} from "antd";

import Util from "../../../assets/js/Util";

import Role from "../../../assets/js/Role";

import {getDivisionList,deleteDivision} from "../../../http/service";

class Index extends Component {

    constructor() {
        super();
        this.state={
            datalist:[]
        }
    }

    componentDidMount() {
        this.getList(1)
    }


    getList=(page)=> {

        page=page||1;

        let pageSize=Util.pageSize

        getDivisionList( {
            role:Role.divisionManager,
            page,
            pageSize

        }).then((data) => {

            this.setState({
                datalist:data
            })

        }).catch(err => {
            message.warning(err)
        })
    }
    add=()=> {
        this.props.history.push('/division/operate')
    }
    adminList=(division_id)=>{

        this.props.history.push({
            pathname:'/division/adminList',
            state:{
                division_id
            }
        })
    }
    edit=(row)=>{

        this.props.history.push({
            pathname:'/division/operate',
            state:{
                opType:'edit',
                formItem:row
            }
        })
    }
    delete=(division_id)=>{

        Util.confirm({
            title:'',
            onOk:()=>{
                deleteDivision({
                    division_id
                }).then(()=>{
                    message.success("")
                    this.getList()
                }).catch(err=>{
                    message.warning(err)
                })
            }
        })

    }



    render() {

        const columns = [
            {
                title: '',
                dataIndex: 'index',
                render:(text,row,index)=>{
                    return index+1;
                }
            },
            {
                title: '',
                dataIndex: 'division_name',
            },
            {
                title: '',
                dataIndex: 'function_level',
                render:(text)=>{
                    let state=''
                    switch (text) {
                        case 0:
                            state=''
                            break;
                        case 1:
                            state=''
                            break;
                        case 2:
                            state=''
                            break;
                        case 3:
                            state=''
                            break;
                    }
                    return state;
                }
            },
            {
                title: '',
                dataIndex: 'create_date',
            },
            {
                title: '',
                dataIndex: 'action',
                render: (text, row) => (
                    <Space size="middle">
                        <Button type={"primary"} size={"small"} onClick={this.edit.bind(this,row)}></Button>
                        <Button type={"primary"} size={"small"} danger onClick={this.delete.bind(this,row.division_id)}></Button>
                        <Button type={"primary"} size={"small"} onClick={this.adminList.bind(this,row.division_id)}></Button>
                    </Space>
                ),
            },
        ];

        return (
            <div>
                <Row>
                    <Col span={22}>
                        <h3></h3>
                    </Col>
                    <Col span={2}>
                        <Button type={"primary"} onClick={this.add}></Button>
                    </Col>
                </Row>
                <Divider/>
                <Row>
                   <Col span={24}>
                       <Table dataSource={this.state.datalist} columns={columns} rowKey="division_id" pagination={false}/>
                   </Col>
                </Row>
            </div>
        );
    }
}

export default Index;