import React, {Component} from 'react';

import {Row, Col, Button, Table, Space, message, Modal, Divider, Pagination} from "antd";

import Util from "../../../assets/js/Util";

import Role from "../../../assets/js/Role";

import {getStationList,deleteStation} from "../../../http/service";

class Index extends Component {

    constructor() {
        super();
        this.state = {
            data: []
        }
    }

    componentDidMount() {
        this.getList(1)
    }


    getList = (page) => {

        page = page || 1;

        let pageSize = Util.pageSize

        getStationList({
            role: Role.divisionManager,
            page,
            pageSize

        }).then((data) => {

            this.setState({
                data: data
            })

        }).catch(err => {
            message.warning(err)
        })
    }
    add = () => {
        this.props.history.push('/station/operate')
    }
    adminList = (division_id) => {

        this.props.history.push({
            pathname: '/division/adminList',
            state: {
                division_id
            }
        })
    }
    edit = (row) => {

        this.props.history.push({
            pathname: '/station/operate',
            state: {
                opType: 'edit',
                formItem: row
            }
        })
    }
    delete = (station_id) => {

        Util.confirm({
            title: '',
            onOk: () => {
                deleteStation({
                    station_id
                }).then(()=>{
                    message.success("")
                    this.getList()
                }).catch(err=>{
                    message.warning(err)
                })
            }
        })

    }

    go2CaseManagerList = (row) => {
        this.props.history.push({
            pathname:'/caseManager/list',
            state:{
                station_id:row.station_id,
                station_name:row.station_name,
            }
        })
    }

    go2TherapistList = (row) => {
        this.props.history.push({
            pathname:'/station/relateTherapist',
            state:{
                station_id:row.station_id,
                station_name:row.station_name,
            }
        })
    }


    render() {

        const columns = [
            {
                title: '',
                dataIndex: 'index',
                render:(text,row,index)=>{
                    return index+1;
                }
            },
            {
                title: '',
                dataIndex: 'station_name',
            },
            {
                title: '',
                dataIndex: 'address',
            },
            {
                title: '',
                dataIndex: 'phone',
            },
            {
                title: '',
                dataIndex: 'action',
                render: (text, row) => (
                    <Space size="middle">
                        <Button size={"small"} type={"primary"} onClick={this.edit.bind(this, row)}></Button>
                        <Button size={"small"} type={"primary"} danger
                                onClick={this.delete.bind(this, row.station_id)}></Button>
                        <Button size={"small"} type={"primary"}
                                onClick={this.go2CaseManagerList.bind(this, row)}></Button>
                        <Button size={"small"} type={"primary"}
                                onClick={this.go2TherapistList.bind(this, row)}></Button>
                    </Space>
                ),
            },
        ];

        return (
            <div>
                <Row>
                    <Col span={22}>
                        <h3></h3>
                    </Col>
                    <Col span={2}>
                        <Button type={"primary"} onClick={this.add}></Button>
                    </Col>
                </Row>
                <Divider/>
                <Row>
                    <Col span={24}>
                        <Table dataSource={this.state.data} columns={columns} rowKey="station_id" pagination={false}/>
                    </Col>
                </Row>
            </div>
        );
    }
}

export default Index;